
$color-primary: #8C0000;
$color-secondary: #C4C4C4;
$color-black: #250000;
$color-white: #ffffff;
$color-quad: #FAFAFA;
$shadow: 0px 4px 4px rgba(196, 196, 196, 0.25);

html {
    background-color: $color-quad;
    font-size: 10px;
}

body {
    box-sizing: border-box;
    margin: 0px;
    overflow-x: hidden;
    max-width: 900px;
    margin: auto;
    padding: 1rem;
    width: 100%;
}

h1, a {
    font-family: 'Raleway', sans-serif;
    margin: 0;
}

h2, p {
    font-family: 'Lato', sans-serif;
    margin: 0;
}

.header-bg {
    background-image: url('../assets/bg-img.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: 100vw;
    height: 90vh;
    position: absolute;
    left: 0;
    top: 0;
    z-index: -100;
}

.header {
    height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .title {
        display: flex;
        justify-content: center;
        align-items: flex-end;
        .logo {
            width: 50%;
        }
        h1 {
            color: $color-primary;
            font-size: 6rem;
            position: relative;
            bottom: 20px;
        }
    }
    h2 {
        color: $color-white;
        font-size: 2rem;
        margin: .5rem;
    }

}

.description {
    display: flex;
    align-items: center;
    justify-content: center;

    &__container {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    svg {
        width: 60%;
        height: auto;
        margin: 2rem;
    }

    h1 {
        color: $color-primary;
        text-align: center;
        font-size: 4rem;
        margin: 1rem 0rem 1rem 0rem;
    }

    p {
        color: $color-black;
        font-size: 1.8rem;
        margin: 2rem 2rem 2rem 2rem;
        
    }
}

.functions {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 5rem;

    &__container {
        display: flex;
        flex-wrap: wrap;
        .col {
            flex: 1;
            min-width: 250px;
            display: flex;
            margin: 3rem;
            align-items: center;
            svg {
                width: 10%;
                height: auto;
            }
            h1 {
                font-size: 2rem;
                color: $color-primary;
                margin-left: 1rem;
                width: 100%;
                text-align: center;
            }
        }
    }
}

.demo {
    width: 100%;
    height: 40vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    a {
        text-align: center;
        color: $color-primary;
        font-size: 4rem;
    }

    p {
        font-size: 1.8rem;
        color: $color-black;
        margin: 2rem;
    }
}

.divider {
    width: 100%;
    height: 30vh;
}

.form {
    width: 100%;
    height: 100vh;
    background-color: $color-white;
    border-radius: 10px;
    box-shadow: $shadow;
    box-sizing: border-box;
    padding: 1rem;
    margin: auto;
    display: flex;
    justify-content: center;
    margin-bottom: 5rem;
    &-iframe-container {
        width: 90%;
        height: 90%;
        margin: auto;
    }

    @media only screen and (max-width: 500px)  {
        width: 100%;
    }
}

